* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  background-image: url("../../image/10036703-1602272026.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  width: 90%;
  height: 80vh;
  margin: 20px auto;
  border: 5px solid rebeccapurple;
  border-radius: 10px;
  background-image: url("../../image/MV5BMzQ5OTUwOTQ4.jpg");
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 20px;
}

.heading__1 {
  color: white;
}

.container p {
  text-align: center;
  width: 80%;
  font-size: 18px;
  font-weight: 600;
  color: white;
}

.movie__value {
  width: 80%;
  height: 50px;
  font-size: 25px;
  padding-left: 30px;
  border-radius: 10px;
  border: none;
  background-color: rgba(255, 255, 255, 0.388);
  font-weight: 600;
}
